*{
    box-sizing: border-box;
}

img{
    max-width: 100%;
}

.video_list_item{
    cursor: pointer;
}

.video_sec{
    margin: 30px 0;
}

.video_info h2{
    margin: 15px 0;
}
.video_info p{
    font-size: 18px;
}

.search_box input{
    height: 50px;
    border-radius: 25px;
    padding: 0 0 0 25px;

}

.search_box .form-control:focus{
    outline: none;
    box-shadow: none!important;
}

